<template>
  <div class="fans-sort">
    <div>
      <img class="img-responsive" src="../assets/contributeSortTop.png">
    </div>
    <div>
      <AwardList :list="awardList"></AwardList>
    </div>
    <div v-if="more" class="lcj-more" @click="loadBottom">
      <span>查看更多</span>
    </div>
    <div v-if="allLoaded" class="no-more">
      <span>已加载完</span>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import AwardList from '@/components/AwardList.vue'
import { getUserTicketSort } from '@/api/ticket'
import { Toast, MessageBox, Indicator, Loadmore } from 'mint-ui';



export default {
  name: 'FansSort',
  components: {
    AwardList
  },
  data() {
    return {
      awardList: [],
      pageNumber: 0,
      more: false,
      allLoaded: false
    }
  },
  created() {
    let titleObject = {
      back: true,
      romoveMarginTop: false,
      title: "粉丝贡献榜"
    }
    this.$emit("routerChange", titleObject);
    this.getUserTicketSort()
  },
  methods: {
    getUserTicketSort: function () {
      let params = {
        pageNumber: this.pageNumber,
        pageSize: 10
      }
      Indicator.open({
        text: '加载中...',
        spinnerType: 'triple-bounce'
      });
      getUserTicketSort(params).then(res => {
        Indicator.close();
        if (res.data.resCode == 0) {
          //成功请求数据
          console.log(res);
          //this.empList = res.data.result;
          this.awardList = this.awardList.concat(res.data.result);
          if (this.awardList.length >= res.data.pagination.totalItems) {
            this.allLoaded = true;
            this.more = false;
          } else {
            this.allLoaded = false;
            this.more = true;
          }
        } else {
          Toast({
            message: res.data.resMsg,
            position: 'middle',
            duration: 3000
          });
        }
      });
    },
    loadBottom: function () {
      console.log("loadBottom");
      this.pageNumber += 1;
      this.getUserTicketSort();

    }
  }
}
</script>
<style lang="scss">
.fans-sort {
  background: #ffffff;
  min-height: 100vh;
}
</style>